import React, { useContext, useEffect, useState } from 'react';
import { ThemeContext } from 'styled-components';
import getCategoryBackgroundColor from '../../helpers/getCategoryBackgroundColor';
import IStatus from '../../interfaces/IStatus';
import ICategory from '../../interfaces/ICategory';
import { BadgeContainer } from './styles';

interface BadgeProps {
  value: string; // Change 'category' to 'value' and specify the data type
}

const Badge: React.FC<BadgeProps> = ({ value }) => {
  const theme = useContext(ThemeContext);

  const [color, setColor] = useState<string>(theme.colors.primary);

  useEffect(() => {
    if (value) {
      switch (value) {
        case ICategory.BUG:
          return theme.colors.bug;
          const valueColor = '#7352CF';
      }
      setColor(valueColor);
    }
  }, [value]);

  return (
    <BadgeContainer color={color}>
      <p>{value}</p>
    </BadgeContainer>
  );
};

export default Badge;
